/* Header */
:root {
  --header-inner-py: 1.35rem;
  --header-logo-py: .4rem;
  --header-logo-height: 4.5rem;
}

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 100;
  transition: background-color 0.6s;
}
.header_nontransparent {
  background-color: #fbfbfb;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
}
.header__inner {
  padding: var(--header-inner-py, 1.35rem) 0;
  display: flex;
  align-items: center;
}
.header__right {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: calc(15px + 2rem + 5.4rem + 3rem);
}
.header-logo {
  padding: var(--header-logo-py, 0.4rem) 1rem;
  max-width: 22rem;
  background: #fbfbfb;
  border-radius: 0.5rem;
}
.header-logo img {
  height: var(--header-logo-height, 4.5rem);
}
.header__menu-trigger {
  position: absolute;
  right: calc(15px + 2rem);
  z-index: 1000;
}
.header.bg-painted {
  background-color: rgba(251, 251, 251, 0.5);
  background-color: rgba(251, 251, 251, 0.95);
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
}
.header.bg-painted .header__contacts-link a {
  color: #151d28;
}
.header.bg-painted .header__contacts-link svg path {
  fill: #151d28;
  stroke: #151d28;
}
.header.bg-painted .menu-trigger__dash {
  background-color: #151d28;
}

.header-menu__logo, .header-menu-trigger, .header-menu-overl {
  display: none;
}

.contacts-link {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  transition: 0.3s;
}
.contacts-link__icon {
  margin-right: 0.5em;
  transition: 0.3s;
}
.contacts-link__icon path {
  transition: inherit;
}
.contacts-link__icon--phone {
  width: 1.05em;
  height: 1.05em;
}
.contacts-link a {
  display: inline-flex;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  color: #7f7f7f;
  transition: color 0.3s;
}

.header-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  transform: translateX(100%);
  z-index: 999;
  width: 75%;
  max-width: 160rem;
  background-color: #ebebeb;
  transition: transform ease-out 0.3s;
}
.header-menu__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 100%;
}
.header-menu__header {
  padding: 15px 30px;
  display: flex;
  align-items: center;
  flex: none;
  min-height: 8rem;
}
.header-menu__logo {
  display: block;
  width: 16rem;
  max-height: 3.2rem;
  flex: none;
  display: none;
}
.header-menu-scrollable {
  overflow-x: hidden;
  overflow-y: auto;
}
.header-menu__body {
  padding-left: 6rem;
  padding-right: 6rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.header-menu__footer {
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: none;
  min-height: 8rem;
}
.header-menu__footer .contacts-link {
  display: none;
}
.header-menu .js-menu-item {
  transform: translateX(5px);
  opacity: 0;
}
.header-menu.open {
  transform: translateX(0);
}
.header-menu.open .js-menu-item {
  transition: 500ms;
  transform: none;
  opacity: 1;
}
.header-menu-overl {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  height: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  visibility: hidden;
  transition: 0.3s;
}
.header-menu-overl.open {
  z-index: 980;
  width: 100%;
  visibility: visible;
  background: rgba(0, 0, 0, 0.5);
}

.menu-trigger {
  --burger-item-translate-y: 670%;
  --burger-item-negative-translate-y: calc(var(--burger-item-translate-y) * (-1));
  display: block;
  width: 5.4rem;
  height: 2.6rem;
  position: relative;
  transition: 0.3s;
}
.menu-trigger__dash {
  position: absolute;
  right: 0;
  display: block;
  height: 2px;
  background-color: #7f7f7f;
  transition: inherit;
}
.menu-trigger__dash:first-child {
  width: 4.32rem;
  top: 0;
}
.menu-trigger__dash:nth-child(2) {
  width: 3.672rem;
  top: 50%;
}
.menu-trigger__dash:last-child {
  width: 5.4rem;
  top: 100%;
}
.menu-trigger.open {
  right: 17px;
}
.menu-trigger.open .menu-trigger__dash:first-child {
  width: 100%;
  transform-origin: center center;
  transform: translate(10%, var(--burger-item-translate-y)) rotate(-45deg);
}
.menu-trigger.open .menu-trigger__dash:nth-child(2) {
  transform: scaleX(0);
  opacity: 0;
}
.menu-trigger.open .menu-trigger__dash:last-child {
  width: 100%;
  transform: translate(10%, var(--burger-item-negative-translate-y)) rotate(45deg);
}

.header-menu__nav {
  display: flex;
  flex: 1;
}

.menu {
  flex: 1;
  position: relative;
}
.menu > .menu-item {
  max-width: 36rem;
}
.menu-item > a {
  display: block;
  padding: 1rem 0;
  font-size: 37px;
  font-weight: 400;
  color: #151d28;
}
.menu-item.current-menu-item > a {
  color: #c90678;
}
.menu .sub-menu {
  padding-top: 1rem;
  position: absolute;
  top: 0;
  left: 36rem;
  right: 0;
  bottom: 0;
  overflow: auto;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}
.menu .sub-menu .menu-item > a {
  padding: 0.6rem 0;
  font-size: 20px;
  font-weight: 400;
  transition: none;
}

.menu-item:hover > a {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(94.72deg, #fe8e2b -12.19%, #ff4b00 108.86%);
}
.menu-item-has-children:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
}

.breadcrumbs {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  font-size: 16px;
}
.breadcrumbs-wrapper__top {
  padding-top: calc(2rem + var(--header-inner-py) * 2 + var(--header-logo-py) * 2 + var(--header-logo-height));
}
.breadcrumbs-sep {
  margin-inline: 0.5em;
  padding-inline: 0.5em;
}
.breadcrumbs a {
  display: inline-block;
  padding: 0.25em 0;
  color: #151d28;
  transition: color 0.1s;
}
.breadcrumbs .current-item {
  padding: 0.25em 0;
}

@media (hover: hover) and (pointer: fine) {
  .menu-trigger:hover .menu-trigger__dash {
    background-color: #151d28;
  }
  .menu-trigger:not(.open):hover .menu-trigger__dash:first-child {
    width: 3.78rem;
  }
  .menu-trigger:not(.open):hover .menu-trigger__dash:nth-child(2) {
    width: 4.482rem;
  }
  .menu-trigger:not(.open):hover .menu-trigger__dash:last-child {
    width: 5.4rem;
  }
  .menu-trigger.open:hover {
    transform: scale(0.92);
  }
  .contacts-link:hover a {
    color: #151d28;
  }
  .contacts-link:hover .contacts-link__icon path {
    fill: #151d28;
    stroke: #151d28;
  }
  .breadcrumbs a:hover {
    color: #d68a03;
  }
}
.menu-trigger:active .menu-trigger__dash {
  background-color: #151d28;
  transition: background-color 0;
}

.menu-trigger:not(.open):active .menu-trigger__dash:first-child {
  transform: translateY(1px);
}
.menu-trigger:not(.open):active .menu-trigger__dash:last-child {
  transform: translateY(-1px);
}

.menu-trigger.open:active {
  transform: scale(0.92);
}
.menu-trigger.open:active .menu-trigger__dash {
  transition: background-color 0;
}

.contacts-link:active a {
  color: #151d28;
  transition: none;
}
.contacts-link:active .contacts-link__icon path {
  fill: #151d28;
  stroke: #151d28;
  transition: none;
}

.menu-item:not(.current-menu-item):active > a {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(94.72deg, #fe8e2b -12.19%, #ff4b00 108.86%);
  transition: none;
}

.breadcrumbs a:active {
  color: #d68a03;
}

/* Entry section */
.entry {
  width: 100%;
  height: 100vh;
}
.entry-slider {
  --swiper-navigation-size: 4.4rem;
  --swiper-navigation-color: #f7f7f7;
  width: 100%;
  height: 100%;
}
.entry-slider .swiper-button-prev,
.entry-slider .swiper-button-next {
  width: calc(var(--swiper-navigation-size) * 1.1);
  height: calc(var(--swiper-navigation-size) * 1.1);
  background: rgba(189, 189, 189, 0.3);
  border-radius: 50%;
  transition: 0.3s;
}
.entry-slider .swiper-button-prev::after,
.entry-slider .swiper-button-next::after {
  font-size: calc(var(--swiper-navigation-size) * 0.5);
  color: var(--swiper-navigation-color);
}
.entry-slider .swiper-button-prev.swiper-button-lock,
.entry-slider .swiper-button-next.swiper-button-lock {
  display: flex;
}
.entry-slider .swiper-button-prev {
  left: 3rem;
}
.entry-slider .swiper-button-next {
  right: 3rem;
}
.entry-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}
.entry-slide a {
  width: 100%;
  height: 100%;
}
.entry-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  user-select: none;
}

@media (hover: hover) and (pointer: fine) {
  .swiper-button-prev:hover,
  .swiper-button-next:hover {
    background: rgba(164, 164, 164, 0.45);
  }
}
.swiper-button-prev:active,
.swiper-button-next:active {
  transition: none;
  background: rgba(164, 164, 164, 0.45);
}

