.clients-slider-wrapper .swiper-button-prev,
.clients-slider-wrapper .swiper-button-next {
  width: calc(var(--swiper-navigation-size) * 1);
  height: calc(var(--swiper-navigation-size) * 1);
  background: rgba(189, 189, 189, 0.3);
  border-radius: 50%;
  transition: 0.3s;
}
