  left: 3rem;
}
.clients-slider-wrapper .swiper-button-next {
  right: 3rem;
}
.clients-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  filter: grayscale(0.65);
  transition: filter 0.3s;
}
