  padding: 1px;
}
.modal-form__inner {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.modal-form .form-checkbox-wrap {
  margin-bottom: 1em;
  font-size: 14px;
}
.modal-form .form-control,
.modal-form .form-control-message {
  padding: 2rem;
}
.modal-form .form-control {
  height: auto;
}
.modal-form .form-label {
  top: 1.5em;
}

/* Modal-message */
.modal-message {
  z-index: 99999;
}
.modal-message__close {
  top: 1rem;
  right: 1rem;
}
.modal-message__close::after, .modal-message__close::before {
  height: 2px;
  background-color: #ff4b00;
}
.modal-message__inner {
  padding: 5rem 3rem;
  min-height: 20rem;
  justify-content: center;
}
.modal-message__content {
  padding-right: unset;
  align-items: center;
}
.modal-message__icon {
  margin-bottom: 2rem;
  display: none;
}
.modal-message__title p {
  margin-bottom: 0.75em;
  font-size: 24px;
}
.modal-message__subtitle {
  text-align: center;
}
.modal-message .modal-backdrop {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.315);
}

.modal-message.shown {
  z-index: 99999;
}

.modal-message_error .modal-message__icon {
  display: none;
}

.modal-message_success .modal-message__icon {
  display: block;
}
.modal-message_success .modal-message__icon path {
  fill: #ff4b00;
}

@media (hover: hover) and (pointer: fine) {
  .modal-message__close:hover {
    transform: scale(0.95);
  }
  .modal-message__close:hover::after, .modal-message__close:hover::before {
    background-color: #ff6f33;
  }
}
.modal-message__close:active {
  transform: scale(0.95);
}
.modal-message__close:active::after, .modal-message__close:active::before {
  background-color: #ff6f33;
}

