.section
  padding: v.$section-py 0
  &_pt-0
    padding-top: 0
  &_py-0
    padding-top: 0
    padding-bottom: 0
  &_pt7-5
    padding-top: 7.5rem
  &_bg-grey
    background-color: #edeeef

  &_light
    *
      color: v.$white

.page-title
  margin-top: 7.5rem

