// vars
$menu-trigger-offset-r: calc(v.$gutter + 2rem)
// $menu-trigger-width: 2.8rem
$menu-trigger-width: 5.4rem
// $menu-trigger-height: 2.1rem
$menu-trigger-height: 2.6rem
// $menu-trigger-dash-height: .3rem
$menu-trigger-dash-height: 2px

/* Header */
:root
  --header-inner-py: 1.35rem
  --header-logo-py: .4rem
  --header-logo-height: 4.5rem
.header
  position: fixed
  top: 0
  right: 0
  left: 0
  z-index: 100
  transition: background-color calc( v.$transition-main * 2 )
  &_nontransparent
    background-color: v.$white
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, .2 )
  &__inner
    padding: var(--header-inner-py, 1.35rem) 0
    display: flex
    align-items: center

  &__right
    display: flex
    align-items: center
    margin-left: auto
    margin-right: calc( $menu-trigger-offset-r + $menu-trigger-width + 3rem )

  &-logo
    padding: var(--header-logo-py, .4rem) 1rem
    max-width: 22rem
    background: v.$white
    border-radius: .5rem
    img
      height: var(--header-logo-height, 4.5rem)

  &__menu-trigger
    position: absolute
    right: $menu-trigger-offset-r
    z-index: 1000

  &.bg-painted
    background-color: rgba(251, 251, 251, .5)
    background-color: rgba(251, 251, 251, .95)
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, .2 )
    .header__contacts-link a
      color: v.$grey-primary
    .header__contacts-link svg path
      fill: v.$grey-primary
      stroke: v.$grey-primary
    .menu-trigger__dash
      background-color: v.$grey-primary

.header-menu
  &__logo,
  &-trigger,
  &-overl
    display: none

.contacts-link
  display: flex
  align-items: center
  flex-wrap: nowrap
  transition: v.$transition-main
  &__icon
    margin-right: 0.5em
    transition: v.$transition-main
    path
      transition: inherit
  &__icon--phone
    width: 1.05em
    height: 1.05em
  a
    display: inline-flex
    align-items: center
    font-size: 20px
    font-weight: 500
    color: v.$grey-light
    transition: color v.$transition-main

.header-menu
  // display: none
  position: fixed
  top: 0
  right: 0
  bottom: 0
  transform: translateX(100%)
  z-index: 999
  width: 75%
  max-width: 160rem
  background-color: v.$header-menu-bg
  transition: transform ease-out 0.3s

  &__inner
    position: relative
    display: flex
    flex-direction: column
    align-items: stretch
    margin-left: auto
    margin-right: auto
    width: 100%
    height: 100%

  &__header
    padding: v.$gutter calc( v.$gutter * 2 )
    display: flex
    align-items: center
    flex: none
    min-height: 8rem
    // box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.2)
    // background: rgba(255, 255, 255, 1)

  &__logo
    display: block
    width: 16rem
    max-height: 3.2rem
    flex: none
    display: none

  &-scrollable
    overflow-x: hidden
    overflow-y: auto

  &__body
    padding-left: 6rem
    padding-right: 6rem
    display: flex
    flex-direction: column
    flex: 1

  &__footer
    padding-top: v.$gutter
    padding-bottom: v.$gutter
    display: flex
    flex-direction: column
    align-items: flex-start
    flex: none
    min-height: 8rem
    .contacts-link
      display: none

    // box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.2)
    // background: rgba(255, 255, 255, 1)

  .js-menu-item
    transform: translateX(5px)
    opacity: 0

  &.open
    transform: translateX(0)
    .js-menu-item
      transition: 500ms
      // transform: translateX(0)
      transform: none
      opacity: 1

  &-overl
    display: block
    position: fixed
    top: 0
    right: 0
    bottom: 0
    height: 0
    z-index: -1
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0)
    visibility: hidden
    transition: 0.3s
    &.open
      z-index: 980
      width: 100%
      visibility: visible
      background: rgba(0, 0, 0, 0.5)

  // &__footer
  //   display: block

.menu-trigger
  --burger-item-translate-y: 670%
  --burger-item-negative-translate-y: calc(var(--burger-item-translate-y) * (-1))

  display: block
  width: $menu-trigger-width
  height: $menu-trigger-height
  position: relative
  transition: v.$transition-main

  &__dash
    position: absolute
    right: 0
    display: block
    // width: 2.8rem
    height: $menu-trigger-dash-height
    background-color: v.$grey-light
    transition: inherit
  &__dash
    &:first-child
      width: calc( $menu-trigger-width * .8 )
      top: 0
    &:nth-child(2)
      width: calc( $menu-trigger-width * .68 )
      // top: calc( ($menu-trigger-height / 2) - ($menu-trigger-dash-height * .5) )
      top: 50%
    &:last-child
      width: $menu-trigger-width
      // top: calc( $menu-trigger-height - $menu-trigger-dash-height )
      top: 100%

  &.open
    right: 17px
    .menu-trigger__dash
      &:first-child
        width: 100%
        transform-origin: center center
        transform: translate(10%, var(--burger-item-translate-y)) rotate(-45deg)
      &:nth-child(2)
        transform: scaleX(0)
        opacity: 0
      &:last-child
        width: 100%

        transform: translate(10%, var(--burger-item-negative-translate-y)) rotate(45deg)

// Menu
// Vars
$menu-item-lv1-max-width: 36rem

.header-menu__nav
  display: flex
  flex: 1

.menu
  flex: 1
  position: relative
  &>.menu-item
    max-width: $menu-item-lv1-max-width
  &-item > a
    display: block
    padding: 1rem 0
    font-size: 37px
    font-weight: 400
    color: v.$grey-primary
  &-item.current-menu-item > a
    color: v.$accent-color

  .sub-menu
    padding-top: 1rem
    position: absolute
    top: 0
    left: $menu-item-lv1-max-width
    right: 0
    bottom: 0
    overflow: auto
    opacity: 0
    visibility: hidden
    transition: v.$transition-main

    .menu-item > a
      // padding: 0.8rem 0
      padding: 0.6rem 0
      font-size: 20px
      font-weight: 400
      transition: none

.menu
  // &-item:not(.current-menu-item):hover > a
  &-item:hover > a
    color: transparent
    -webkit-background-clip: text
    background-clip: text
    background-image: linear-gradient(94.72deg, v.$yellow-start -12.19%, v.$yellow-end 108.86%)

  &-item-has-children:hover > .sub-menu
    opacity: 1
    visibility: visible

// Breadcrumbs
.breadcrumbs
  display: flex
  align-items: center
  flex-wrap: wrap
  font-size: 16px
  &-wrapper__top
    padding-top: calc( 2rem + (var(--header-inner-py) * 2) + (var(--header-logo-py) * 2) + var(--header-logo-height) )
  // &-wrapper__bottom
  //   padding-top: .5rem 
  //   padding-bottom: .5rem
  //   background-color: #f1f1f1
  &-sep
    margin-inline: .5em
    padding-inline: .5em
  a
    display: inline-block
    padding: .25em 0
    color: v.$grey-primary
    transition: color calc( v.$transition-main / 3 )
  .current-item
    padding: .25em 0

@media (hover: hover) and (pointer: fine)
  .menu-trigger:hover
    .menu-trigger__dash
      background-color: v.$grey-primary

  .menu-trigger:not(.open):hover
    .menu-trigger__dash
      &:first-child
        width: calc( $menu-trigger-width * .7 )
      &:nth-child(2)
        width: calc( $menu-trigger-width * .83 )
      &:last-child
        width: $menu-trigger-width

  .menu-trigger.open:hover
    transform: scale(.92)

  .contacts-link:hover
    a
      color: v.$grey-primary
    .contacts-link__icon path
      fill: v.$grey-primary
      stroke: v.$grey-primary

  .breadcrumbs a:hover
    color: v.$bc-link-hover

// Active
.menu-trigger:active
  .menu-trigger__dash
    background-color: v.$grey-primary
    transition: background-color 0

.menu-trigger:not(.open):active
  .menu-trigger__dash
    &:first-child
      transform: translateY(1px)
    &:last-child
      transform: translateY(-1px)

.menu-trigger.open:active
  transform: scale(.92)
  .menu-trigger__dash
    transition: background-color 0

.contacts-link:active
  a
    color: v.$grey-primary
    transition: none
  .contacts-link__icon path
    fill: v.$grey-primary
    stroke: v.$grey-primary
    transition: none

.menu
  &-item:not(.current-menu-item):active > a
    // color: v.$yellow-start
    color: transparent
    -webkit-background-clip: text
    background-clip: text
    background-image: linear-gradient(94.72deg, v.$yellow-start -12.19%, v.$yellow-end 108.86%)
    transition: none

.breadcrumbs a:active
  color: v.$bc-link-hover

/* Entry section */
.entry
  width: 100%
  height: 100vh

  &-slider
    --swiper-navigation-size: 4.4rem
    // --swiper-navigation-color: #c90678
    --swiper-navigation-color: #f7f7f7
    // --swiper-navigation-color: #151d28
    
    width: 100%
    height: 100%
    .swiper-button-prev,
    .swiper-button-next
      width: calc( var(--swiper-navigation-size) * 1.1 )
      height: calc( var(--swiper-navigation-size) * 1.1 )
      background: rgba(189, 189, 189, 0.3)
      border-radius: 50%
      // box-shadow: v.$swiper-nav-shadow
      transition: v.$transition-main
      &::after
        // font-size: calc(var(--swiper-navigation-size) * .3)
        font-size: calc(var(--swiper-navigation-size) * .5)
        color: var(--swiper-navigation-color)
      &.swiper-button-lock
        display: flex
    .swiper-button-prev
      left: 3rem
    .swiper-button-next
      right: 3rem
  &-slide
    display: flex
    justify-content: center
    align-items: center
    a
      width: 100%
      height: 100%
    img
      width: 100%
      height: 100%
      object-fit: cover
      user-select: none

// Entry Hover
@media (hover: hover) and (pointer: fine)
  .swiper-button-prev:hover,
  .swiper-button-next:hover
    background: darken(rgba(189, 189, 189, 0.45), 10%)
    // &::after
    //   color: darken(#f7f7f7, 10%)

// Entry Active
.swiper-button-prev:active,
.swiper-button-next:active
  transition: none
  background: darken(rgba(189, 189, 189, 0.45), 10%)
  // &::after
  //   color: darken(#f7f7f7, 10%)

/* Services section */
.services
  &__title
    margin-bottom: v.$section-py
    max-width: 92rem

.tabs
  display: flex
  flex-wrap: wrap
  margin-bottom: 5rem

  &__btn
    margin-bottom: 1rem
    font-weight: 400
    color: $btn-grey-color
    background-color: $btn-grey-bg

  &__btn:not(:last-child)
    margin-right: 1rem

  &__btn.active
    color: v.$white
    // background: linear-gradient(#FBFBFB 0 0) padding-box, linear-gradient(90deg, #fe672e 0%, #c90678 100%) border-box
    background: $btn-grey-bg linear-gradient(0deg, #ce0c6d 0%, #fe692b 100%)
    background-position: center 50%
    background-size: 100% 100% 
    // border: 1px solid transparent

  &-content
    position: relative
    // overflow: hidden

  &-content__item
    position: absolute
    top: 0
    right: 0
    bottom: 0
    left: 0
    z-index: 1
    width: 100%
    opacity: 0
    transition: opacity 0.3s

  &-content__item.active
    position: relative
    z-index: 5
    opacity: 1

  &-content__item h3
    margin: 10px auto

@media (hover: hover) and (pointer: fine)
  .tabs__btn:hover
    background-color: darken($btn-grey-bg, 10%)
  .tabs__btn.active:hover
    // color: v.$grey-primary
    // background-image: linear-gradient(93deg, rgba(0,0,255,1) 0%, rgba(184,0,255,1) 100%)
    // background-image: linear-gradient(90deg, #fe672e 0%, #c90678 100%)
    // background: linear-gradient(#FBFBFB 0 0) padding-box, linear-gradient(90deg, #fe672e 0%, #c90678 100%) border-box
    background-size: 100% 200% 
    background-position: center 100%

// Actives
.tabs__btn:active
  background-color: darken($btn-grey-bg, 10%)
.tabs__btn.active:active
  // color: v.$grey-primary
  // background-image: linear-gradient(93deg, rgba(0,0,255,1) 0%, rgba(184,0,255,1) 100%)
  // background-image: linear-gradient(90deg, #fe672e 0%, #c90678 100%)
  // background: linear-gradient(#FBFBFB 0 0) padding-box, linear-gradient(90deg, #fe672e 0%, #c90678 100%) border-box
  background-size: 100% 200% 
  background-position: center 100%

.service-item
  display: flex
  justify-content: space-between
  align-items: flex-start
  h3
    margin-bottom: 1em
  p
    font-size: 17px

  &_reverse
    flex-direction: row-reverse
    justify-content: flex-end
    .service-item__content
      padding-left: calc( 4 * v.$gutter )
      max-width: 46rem

  &__content
    width: calc( 45% - v.$gutter )
    max-width: 40rem
  &__thumb
    position: relative
    width: calc( 55% - v.$gutter )
    max-width: 60rem
    // border-radius: 2rem
    border-radius: 1rem
    box-shadow: 0px 4px 34px 0px rgba(217, 217, 217, 1)
    overflow: hidden
    &::after
      content: ''
      display: block
      width: 100%
      height: 0
      padding-bottom: 54%
    img
      position: absolute
      top: 0
      right: 0
      bottom: 0
      left: 0
      width: 100%
      height: 100%
      object-fit: cover

  &__content-footer
    margin-top: 6rem
    .order-link
      margin-left: 1.5rem

.order-link
  display: inline-flex
  align-items: center
  color: v.$grey-primary
  font-size: 15px
  font-weight: 400
  text-decoration: underline
  &::before
    content: ''
    display: inline-block
    margin-right: 0.5em
    width: 1.27em
    height: .87em
    background: url(../img/icons/eye.png) center no-repeat
    background-size: contain
    transition: transform v.$transition-main

@media (hover:hover) and (pointer:fine)
  .order-link:hover::before
    transform: scale(1.1)

.order-link:active::before
  transform: scale(1.1)
  transition: none

/* Advantages section */
.advantages
  --item-content-max-width: 78rem
  &__title
    max-width: 80rem
  &__box
    position: relative
    overflow: hidden
  &__box-img
    position: absolute
    top: 0
    left: 0
    bottom: 0
    z-index: 0
    display: flex
    justify-content: flex-start
    align-items: center
    img
      height: 90%
  &__item
    display: flex
    justify-content: flex-end
    padding: 5rem 2.5rem 5rem 0
    min-height: 20rem
    max-height: 24rem
    position: relative
    z-index: 1
    border-radius: 10px
    overflow: hidden
  &__item-content
    max-width: var(--item-content-max-width)
    display: flex
  &__item-title
    flex: none
    padding-right: calc( v.$gutter * 2 )
    width: 30rem
    h3
      display: inline-flex
      align-items: baseline

  &__item-descr
    font-size: 15px
    p
      margin-bottom: unset
      font-size: 15px

@media (hover: hover) and (pointer: fine)
  .advantages__item:hover
    &::after
      opacity: .3
      transform: scale(1)

/* Clients section */
.clients
  &-slider

    width: 100%
    max-width: 1320px
    height: 100%
  &-slider-wrapper
    --swiper-navigation-size: 4.4rem
    // --swiper-navigation-color: #c90678
    --swiper-navigation-color: #7f7f7f
    position: relative
    height: 16rem

    .swiper-button-prev,
    .swiper-button-next
      width: calc( var(--swiper-navigation-size) * 1 )
      height: calc( var(--swiper-navigation-size) * 1 )
      // color: v.$white
      background: rgba(189, 189, 189, 0.3)
      border-radius: 50%
      // box-shadow: v.$swiper-nav-shadow
      transition: v.$transition-main
      &::after
        font-size: calc(var(--swiper-navigation-size) * .4)
      &.swiper-button-lock
        display: flex
    .swiper-button-prev
      left: 3rem
    .swiper-button-next
      right: 3rem

  &-slide
    display: flex
    justify-content: center
    align-items: center
    filter: grayscale(.65)
    transition: filter v.$transition-main

    img
      max-height: 14rem

@media (hover:hover) and (pointer: fine)
  .clients-slider-wrapper
    .swiper-button-prev:hover,
    .swiper-button-next:hover
      &::after
        color: darken(#7f7f7f, 10%)

.clients-slide:hover
  filter: grayscale(0)

// Actives
.clients-slide:active
  filter: grayscale(0)

.clients-slider-wrapper
  .swiper-button-prev:active,
  .swiper-button-next:active
    &::after
      color: darken(#7f7f7f, 10%)

.clients-slider-wrapper
  .swiper-button-prev:active,
  .swiper-button-next:active
    transition: none
    &::after
      color: var(--swiper-navigation-color)

