    &:nth-child(2)
      margin-bottom: 7rem

  &__cap 
    font-size: 24px
    margin-bottom: 2.4rem

  &__data 
    width: calc( 65% - v.$gutter )
    display: flex
    justify-content: space-between
    flex-wrap: wrap
    .contacts__item 
      width: calc(50% - v.$gutter)

  &__info,
  &__info--connect 
    a 
      text-decoration: underline
      border-bottom: none
      font-size: 17px
      color: v.$grey-primary
      transition: all .1s

  &__info 
    padding-left: 5px
    font-size: 17px
    p
      margin-bottom: 0.5em
      strong
        font-weight: 500

  &__info--connect 
    display: flex
    flex-direction: column
    a 
      margin-bottom: 15px
      font-size: 17px
      display: flex
      align-items: center


  &__info-social-links 
    display: flex
    align-items: center
    &>a 
      margin-right: 10px
    &>a>svg 
      width: 30px
      height: 30px
      fill: #000000
    &>a>svg path 
      transition: .3s

  &__info--form 
    text-align: center

  .input[type="submit"] 
    margin: 0 auto
    display: block

  .ajax-loader 
    display: none




.contacts__sidebar
  width: calc( 35% - v.$gutter )
  input 
    width: 100%

  .btn 
    margin: 0 auto
    display: block

  .contacts__cap 
    text-align: center








.feedbackFormField .wpcf7-text,
.feedbackFormField .wpcf7-textarea 
  width: auto





.contacts_map 
  word-wrap: break-word
  height: 400px
  position: relative
  overflow: hidden



.contacts__info-social-links a>svg 
  transition: inherit


@media (hover:hover) and (pointer: fine)
  .contacts__info a:hover,
  .contacts__info--connect a:hover 
    color: v.$yellow-end
    

.contacts__info-social-links a:hover>svg 
  transform: translateY(-1px)

.contacts__info-social-links a:hover .viber-icon .icon-img 
  fill: #69207d

.contacts__info-social-links a:hover .whatsapp-icon .icon-img 
  fill: #2c923a

.contacts__info-social-links a:hover .telegram-icon .icon-img 
  fill: #287ca0

.contacts__info-social-links a:hover .telegram-icon .icon-bg 
  stroke: #287ca0


.contacts__info a:active,
.contacts__info--connect a:active 
  color: v.$yellow-end







@media(max-width: 991.98px) 
  .contacts__contacts 
    flex-direction: column
  .contacts__item
    &:first-child,
    &:nth-child(2)
      margin-bottom: 5rem
  .contacts__item
    margin-bottom: 5rem


  .contacts__data,
  .contacts__sidebar
    width: 100%

  .contacts__info 
    padding-left: unset





@media(max-width: 575.98px) 
  .contacts__data 
    flex-direction: column
    align-items: flex-start
  .contacts__data .contacts__item 
    width: 100%
    max-width: 45rem
  .contacts__sidebar .contacts__cap
    text-align: left
  
















/* Order modal */
.order-modal
  &__inner
    padding-top: 2rem
  &__title
    text-align: center
    font-size: 24px
    p
      margin-bottom: 0.5em
  &__subtitle
    text-align: center
    font-size: 18px
    p
      margin-bottom: 1em
  .modal__content
    overflow-x: hidden
    overflow-y: auto
    &::-webkit-scrollbar
      width: 5px
      background: #eeeeee
      border-radius: 5px
    &::-webkit-scrollbar-thumb
      background: transparentize(v.$grey-primary, .5)
      border-radius: 5px

                // ::-webkit-scrollbar-button
                // ::-webkit-scrollbar
                // ::-webkit-scrollbar-button
                // ::-webkit-scrollbar-thumb
                // ::-webkit-scrollbar-track

/* Modal form */
.modal-form
  &-wrap
    padding: 1px
  &__inner
    display: flex
    flex-direction: column
    flex-wrap: wrap
  .form-checkbox-wrap
    margin-bottom: 1em
    font-size: 14px
  .form-control,
  .form-control-message
    padding: 2rem
  .form-control
    height: auto
  .form-label
    top: 1.5em


/* Modal-message */
$modal-close-color: v.$yellow-end
.modal-message
  z-index: 99999
  &__close
    top: 1rem
    right: 1rem
  &__close::after,
  &__close::before
    height: 2px
    background-color: $modal-close-color
  &__inner
    padding: 5rem 3rem
    min-height: 20rem
    justify-content: center
  &__content
    padding-right: unset
    align-items: center
  &__icon
    margin-bottom: 2rem
    display: none
  &__title
    p
      margin-bottom: 0.75em
      font-size: 24px
  &__subtitle
    text-align: center

      
  .modal-backdrop
    display: block
    position: absolute
    top: 0
    right: 0
    bottom: 0
    left: 0
    z-index: 1
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.315)

.modal-message.shown
  z-index: 99999

.modal-message_error
  .modal-message__icon
    display: none

.modal-message_success
  .modal-message__icon
    display: block
    path
      fill: v.$yellow-end





@media (hover:hover) and (pointer:fine)
  .modal-message
    &__close:hover
      transform: scale(.95)
    &__close:hover::after,
    &__close:hover::before
      background-color: lighten($modal-close-color, 10%)
 


.modal-message
  &__close:active
    transform: scale(.95)
  &__close:active::after,
  &__close:active::before
    background-color: lighten($modal-close-color, 10%)
    



